@use "variables" as v;

h1,
h2,
h3 {
  font-family: v.$fuente_principal;
  margin: 0 0 v.$separacion 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  color: v.$primario;

  span {
    color: v.$secundario;
    font-size: 1.8rem;
    display: block; /* fuerza un salto de linea */
  }

  &:after {
    content: "";
    display: block;
    width: 10rem;
    height: 10rem;
    margin: 0 auto;
    background-image: url(../img/cafe.svg);
    background-size: 10rem; /* la imagen definida en el background ajusta su tamaño a 10 rem */
  }
}

h3 {
  font-size: 3.2rem;
}

.heading-blanco {
  @extend h2; /* heredamos todos los atributos del h2 */
  color: v.$blanco;

  span {
    color: v.$blanco;
  }

  &:after {
    display: none;
  }
}
