@use "base/variables" as v;
@use "base/mixin" as m;

.nuestro-menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg),
    linear-gradient(to top, v.$primario, v.$primario);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%; /* aqui la primera imagen está centrada y con un padding de 5 rem, la segunda imagen esta centrada y a un 92% del tamaño del contenedor */
  background-size: 200%;
  padding: 15rem 2rem 20rem 2rem;

  @include m.tablet {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
  @include m.desktop {
    background-size: 50%, 50%, 100%; /* primera-imagen, segunda-imagen, fondo */
  }
}

.grid-menu {
  @include m.tablet {
    @include m.grid(2, 2rem);
  }
}

.menu {
  h3 {
    color: v.$blanco;
  }

  li {
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      color: v.$blanco;
      font-size: 2.2rem;
      line-height: 1.5;
      margin: 0 0 0 2rem;

      span {
        display: block;
        color: v.$secundario;
      }

      .precio {
        font-size: 4rem;
        font-weight: 700;
      }
    }
  }
}
