@use "base/variables" as v;
@use "base/mixin" as m;

.iconos {
  margin-top: 5rem;

  .lista-iconos {
    @include m.tablet {
      @include m.grid(3, 2rem);
    }

    li {
      img {
        margin: 0 auto;
        width: 12rem;
      }
      p {
        font-family: v.$fuente_cursiva;
        font-size: 3.2rem;
        color: v.$primario;
      }
    }
  }
}
