/* para incluir la variable que esta definida en otro archivo, hay que utilizar @use para importar ese archivo y luego para hacer referencia a la variable 
definida en ese archivo ponemos -> nombredelarchivo.$variable */
body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
.heading-blanco,
h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #5e311e;
}
h2 span, .heading-blanco span {
  color: #c39c63;
  font-size: 1.8rem;
  display: block; /* fuerza un salto de linea */
}
h2:after, .heading-blanco:after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-size: 10rem; /* la imagen definida en el background ajusta su tamaño a 10 rem */
}

h3 {
  font-size: 3.2rem;
}

.heading-blanco {
  /* heredamos todos los atributos del h2 */
  color: #ffffff;
}
.heading-blanco span {
  color: #ffffff;
}
.heading-blanco:after {
  display: none;
}

/* 

Para incluir los mixin en otro archivo primero devemos linkear el archivo donde estos mixxin se encuentran con el codigo:

@use 'direccion/mixin.scss'

y luego dentro del selector que quieras usarlo puedes añadirlo con la palabra clave @include:

.header{
    padding: 5rem 0;
    position: relative;
    @include mixin.color
}

color es un selector que he definido previamente en el archivo mixin

@mixin color {
  color: black;
  font-size: 2rem;
  text-decoration: none;
}

Tambien podemos definir mixin dinamico que asignara al background el color definido en la llamada de la funcion 

@mixin bgColor($color){
    background-color: $color;
}

*/
/*
El @content, también conocido como directiva de contenido, es “ simple ”, ya que proporciona una forma de reducir el código repetitivo, lo que permite la reutilización 
y cambios más fáciles a lo largo de la base de código.

Usar @content para :

@mediaqueries,
keyframes animation,
botones,
selectores anidados,
notificaciones
*/
/* este archivo va actuar como un indice de todos los archivos .scss */
/*

@forward -> simplmente incluye archivos dentro de otros archivos

@use -> Incluyen y utilizan su contenido

@import -> version anterior de @use, tiene las mismas funcionalidades

*/
/* Es necesario el guion bajo inicial para decirle a sass que este archivo va a ser incluido en otro y solo puede ser llamado a traves de ese archivo raiz; es decir;
los archivos que empiezan por un guion bajo no pueden ser llamados de forma independiente */
.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  /* llamamos a los mixin declarados */
  /* el & hace que la anidacion de elementos elimine el espacio de tal manera que al compilar el css esto quedaria .header::before{} */
  /*
    &::before {
      content: "";
      background-color: v.$negro;
      position: absolute;
      inset: 0;  inset es el shorthand para top right bottom left 
      opacity: 0.6;
    }
  */
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
@media (min-width: 480px) {
  .header {
    padding: 4rem;
    /* se trae las propiedades que definas en el archivo donde este es llamado */
  }
}
@media (min-width: 768px) {
  .header {
    padding: 3rem;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 2rem;
  }
}
.header::before {
  content: "";
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header .contenido-header {
  position: relative;
}
.header .contenido-header h1 {
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin-top: 10rem;
  }
}

