para esto primero necesitas instalar dependencias en gulp

npm i --save-dev sass gulp-sass
npm install --save-dev gulp-imagemin@7.1.0 -> para trabajar con imagenes, si instalamos versiones superiores no funcionará
npm i --save-dev gulp-webp
npm i --save-dev gulp-sourcemaps -> nos mapea todo el codigo diciendo en que archivo se encuantra cada linea de codigo
npm i --save-dev cssnano -> minifica nuestro css eliminando espacios en blanco y mejora nuestro codigo

